body {
  margin: 0;
  font-family: "Nunito Sans", sans-serif;

  background-color: hsl(0, 0%, 98%);

  background-color: var(--primaryBg-color);
  margin-bottom: 45px;
  width: 100%;
}

:root {
  --primaryBg-color: hsl(0, 0%, 98%);
  --secondaryBg-color: hsl(0, 0%, 100%);
  --font-color: hsl(200, 15%, 8%);
  --font-color2: #848484;
  --font-size: 16px;
  --font-size2: 14px;
  --border-radius: 5px;
}

[dark-theme="dark"] {
  --primaryBg-color: hsl(207, 26%, 17%);
  --secondaryBg-color: hsl(207, 23%, 22%);
  --font-color: hsl(0, 0%, 100%);
  --font-color2: hsl(0, 0%, 100%);
  --border-radius: 5px;
}

.header {
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  box-shadow:  0px 2px 4px rgba(0, 0, 0, 0.0562443);
  height: 80px;
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.header__container {
  display: flex;
  padding: 0;
  width: 1280px;
  align-items: center;
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  margin-left: 5.62%;
  margin-right: 5.56%;
}

h1 {
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  font-style: normal;
  font-weight: 800;
  font-size: 34px;
  line-height: 33px;
}

.header__container > div {
  display: flex;
  margin-left: auto;
  width: 108px;
  height: 22px;
  padding: 0;
}

.theme_switch:hover {
  cursor: pointer;
}

.theme_switch > p {
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  border: 0;
  font-size: 16px;
  font-size: var(--font-size);
  font-weight: 600;
  margin: 0;
  padding: 0;
  align-items: center;
  position: absolute;
  margin-left: 29px;
  margin-right: 0%;
  line-height: 22px;
}

#theme_logo {
  height: 20px;
  width: 19px;
  margin-right: 0px;
  position: absolute;
  align-self: center;
}

.display_top {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}

.display_bottom {
  margin: 0;
  padding: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
}

.filter_container {
  display: flex;
  margin-left: 5.62%;
  margin-right: 5.56%;
  flex-wrap: wrap;
  max-width: 1280px;
  width: 88.82%;
  margin-top: 40px;
}

.Loader {
  font-size: 22px;
  
  /* margin-top: 15%; */
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
}

.search_button {
  display: flex;
  align-items: center;
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  height: 50px;
  border: 0;
  border-radius: 5px;
  border-radius: var(--border-radius);
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  font-size: 14px;
  font-size: var(--font-size2);
  box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.0532439);
  padding: 0;
  margin: 0;
  width: 480px;
  height: 56px;
  margin-bottom: 2px;
  text-align: left;
}
.search_button > svg {
  height: 15px;
  width: 15px;
  margin: 0;
  margin-left: 32px;
  padding: 0;
}

.search_bar {
  font-family: "Nunito Sans", sans-serif;
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  border: 0;
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  font-size: 14px;
  font-size: var(--font-size2);
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  height: 30px;
  margin-left: 27px;
}
input:focus {
  outline: none;
}
::-webkit-input-placeholder {
  color: #848484;
  color: var(--font-color2);
  opacity: 1;
  font-weight: 400;
  line-height: 20px;
}
::placeholder {
  color: #848484;
  color: var(--font-color2);
  opacity: 1;
  font-weight: 400;
  line-height: 20px;
}
.button_container {
  display: block;
  border-radius: 5px;
  border-radius: var(--border-radius);
  height: 56px;
  align-items: flex-end;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: auto;
}

.filter_button {
  display: flex;
  align-items: center;
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  height: 50px;
  border: 0;
  border-radius: 5px;
  border-radius: var(--border-radius);
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);

  font-weight: 400;
  line-height: 20px;
  box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.0532439);
  padding: 0;
  margin: 0;
  width: 200px;
  height: 56px;
  margin-bottom: 2px;
  text-align: left;
  padding-left: 24px;
  padding-right: 18px;
}
.filter_button:hover {
  cursor: pointer;
}
.filter_text {
  font-family: "Nunito Sans", sans-serif;
  margin-right: 0px;
  font-size: 14px;
  font-size: var(--font-size2);
  font-weight: 400;
}
.filter_button > svg {
  height: 12px;
  width: 12px;
  margin: 0;
  margin-left: auto;
  padding: 0;
}

.dropdown_content {
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  font-size: 14px;
  font-size: var(--font-size2);
  font-weight: 400;
  line-height: 20px;
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.0532439);
  border-radius: 5px;
  border-radius: var(--border-radius);
  display: block;
  text-align: left;
  height: 164px;
  width: 200px;
  position: relative;
  margin-left: auto;
}

li {
  list-style: none;
  font-family: "Nunito Sans", sans-serif;
}

li:hover {
  cursor: pointer;
  color: #5c8e90;
  
}

#Africa,
#Asia,
#Americas,
#Europe,
#Oceania {
  height: 20px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 7.5px;
  margin-left: 24px;
  padding: 0;
}

#Africa {
  padding-top: 16px;
}

.card_container {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  margin-left: 5.62%;
  margin-right: 5.56%;
  margin-top: 48px;
  border-radius: 5px;
  justify-content: space-between;
  grid-row-gap: 75px;
  row-gap: 75px;
  max-width: 1280px;
}

.card {
  font-family: "Nunito Sans", sans-serif;
  width: 264px;
  height: 336px;
  border-radius: 5px;
  border: 0;
  background-color: hsl(0, 0%, 100%);
  background-color: var(--secondaryBg-color);
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
  box-shadow:  0px 0px 7px 2px rgba(0, 0, 0, 0.0294384);
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}
.card:hover {
  cursor: pointer;
}
.flag {
  width: auto;
  border-radius: 5px 5px 0 0;
  margin: 0;
  padding: 0;
}

.flag > img {
  width: 264px;
  height: 160px;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
  margin: 0;
  padding: 0;
}

.country-details {
  text-align: left;
  margin-left: 24px;
  margin-top: -34px;
  padding: 0;
}

.country_name {
  width: 240px;
  padding: 0;
  margin: 0;
  margin-top: 24px;
  margin-bottom: 16px;
}

.country_name > p {
  padding: 0;
  margin: 0;
  font-size: 18px;
  font-weight: 800;
}

.country_info {
  text-align: left;
  padding: 0;
  margin: 0;
  width: 240px;
  height: 64px;
  font-size: 14px;
  font-size: var(--font-size2);
  line-height: 16px;
  line-height: var(--font-size);
  font-weight: 600;
}

.country_info > div {
  margin-bottom: 5px;
}
span {
  font-weight: 300;
}
.spaceShift {
  width: 264px;
}

.errorInfo {
  color: hsl(200, 15%, 8%);
  color: var(--font-color);
}

